const RandomButton = props => (
	<div className="random-btn">
		<img
			width="40px"
			height="40px"
			src="data:image/svg+xml;base64,
"
		/>
		<div className="pulse" />
		<style jsx>{`
			.random-btn {
				position: fixed;
				bottom: 20px;
				border: 3px solid #2fa799;
				right: 30px;
				cursor: pointer;
				height: 70px;
				width: 70px;
				border-radius: 50%;
				background-color: #3ad4c3;
				display: flex;
				justify-content: center;
				align-items: center;
				transition: 0.4s;
				box-sizing: border-box;
			}
			.pulse {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				border-radius: 9999px;
				background-color: #3ad4c3;
				opacity: 0.9;
				z-index: -1;
				pointer-events: none;
				animation: sonarWave 2s linear infinite;
			}

			@keyframes sonarWave {
				from {
					opacity: 0.4;
				}
				to {
					transform: scale(2);
					opacity: 0;
				}
			}
		`}</style>
	</div>
);

export default RandomButton;



